export default {
  data() {
    return {
      loading: false,
      formInline: {
        user: '',
        password: ''
      },
      ruleInline: {
        user: [
          {
            required: true,
            message: '请输入用户名',
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: '请输入密码',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  methods: {
    handleSubmit(name) {
      //验证
      let self = this
      self.$refs[name].validate(valid => {
        if (valid) {
          self.login() //登录
        }
      })
    },
    login() {
      let self = this
      self.loading = true
      let url = api.main + 'user/admin/login'
      let param = {
        phone: self.formInline.user,
        password: self.formInline.password
      }
      util
        .post(url, param)
        .then(res => {
          //成功
          if (util.isSuccess(res)) {
            Cookies.set('token', res.data.data.token, { expires: 7 })
            //sessionStorage.setItem('token', res.data.data.token)
            self.$Message.success('登录成功！')
            self.loading = false
            self.$router.push('/')
          }
        })
        .catach(err => {
          //失败
          console.log(err)
        })
        .finally(() => {
          self.loading = false
        })
    }
    // handlerLogin(name) {
